.page {
  overflow-y: hidden;
  position: relative;
}

.myBtn {
  outline: none;
  border: none;
  background-color: transparent;
}

.hidden {
  display: none;
}

.header__list__nav li:nth-child(1) {
  margin-left: 0;
}

.no-style {
  list-style: none;
  padding: 0;
  margin: 0;
}

.title-block {
  display: inline-block;
  font-family: "Times New Roman", serif;
  font-size: 1.80vw;
  padding: 0.5vw 1.4vw 0.7vw 0.6vw;
  background-color: #908a8a;
  margin-bottom: 0.5%;

  @media (max-width: 897px) {
    margin-top: 3px;
    padding: 1.2vw 2.7vw 1vw 2.2vw;
    letter-spacing: 1.6px;
    font-size: 2.55vw;
    margin-bottom: 3.5%;
  }

  @media (max-width: 500px) {
    padding: 0.2vw 2.7vw 1vw 2.2vw;
    margin-top: 5vw;
    font-size: 4vw;
    margin-bottom: 1%;
  }
}

ol {
  color: #344760;
  @media (max-width: 500px) {
    margin-bottom: 2vw;
  }

  li {
    letter-spacing: 0.7px;
    margin-top: 0;
    color: #344760;
    font-size: 1.8vw;
    line-height: 2.6vw;
    @media (max-width: 897px) {
      letter-spacing: 0.2px;
      line-height: 2.9vw;
    }
    @media (max-width: 500px) {
      line-height: 4.7vw;
      font-size: 3.8vw;
      line-height: 4.3vw;
      letter-spacing: -0.1px;
      margin-bottom: 1.8vw;
    }
  }
}

.sublist {
  margin-top: 0vw;
  padding-left: 2vw;
  @media (max-width: 500px) {
    padding-left: 5vw;
  }
}

.highlighted-text {
  background-color: rgb(255, 255, 255, 0.5);
  border-radius: 20px;
  padding: 1.9% 1.5%;
  letter-spacing: 1px;
  color: #344760;
  font-size: 1.8vw;
  line-height: 2.6vw;
  margin-bottom: 2.2vw;

  @media (max-width: 897px) {
    border-radius: 5px;
  }

  @media (max-width: 500px) {
    font-size: 3.8vw;
    line-height: 4.3vw;
    letter-spacing: -0.1px;
    margin-bottom: 3vw;
  }
}

.ps__thumb-y {
  width: 1.3vw;
  background-color: rgb(255, 255, 255, 0.3) !important;
  @media (max-width: 897px) {
    display: none;
    width: 1vw;
  }
  @media (max-width: 500px) {
    width: 2vw;
  }

  &:focus, &:hover {
    width: 1.3vw;
    background-color: rgb(255, 255, 255, 0.3);
    @media (max-width: 897px) {
      width: 1vw;
    }
    @media (max-width: 500px) {
      width: 2vw;
    }
  }
}

.ps__rail-y:hover > .ps__thumb-y {
  width: 1.3vw;
  background-color: rgb(255, 255, 255, 0.3);
  @media (max-width: 897px) {
    width: 1vw;
  }
  @media (max-width: 500px) {
    width: 2vw;
  }
}


.ps .ps__rail-y {
  @media (max-width: 897px) {
    display: none;
  }

  &:hover, &:focus > .ps__thumb-y {
    width: 1.3vw;
    background-color: rgb(255, 255, 255, 0.3);
    @media (max-width: 897px) {
      width: 1vw;
    }
    @media (max-width: 500px) {
      width: 2vw;
    }
  }


  &:focus, .ps__rail-y:hover {
    background-color: transparent;
  }

  &:hover {
    background-color: transparent;
  }
}

.ps .ps__rail-y.ps--clicking {
  background-color: transparent;
  @media (max-width: 897px) {
    display: none;
  }
}


.ps__rail-y.ps--clicking .ps__thumb-y {
  width: 1.3vw;
  @media (max-width: 897px) {
    width: 1vw;
    display: none;
  }
  @media (max-width: 500px) {
    width: 2vw;
  }
}

.ref-underlined {
  font-style: italic;
  border-bottom: 1px solid #344760;;
}

.btn-wrapper {
  text-align: center;
}

.action-btn {

  text-shadow: 0px 1.5px 0.5px #ba632b, 0px -1.5px 0.5px #fec59f, 1.5px 0px 0.5px #df996d, -1.5px 0px 0.5px #df996d;
//  ...
  //padding: 13px 31px 13px;   // px 
  padding: 1.2vw 8.3vw 1.3vw;
  border-radius: 16px;
  border: none;
  outline: none;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.9);
  background-color: #FFAA74;
  font-size: 1.4vw;
  letter-spacing: 2.3px;
  font-weight: bold;
  text-align: center;
  color: #fff;
  margin-bottom: 0;
  // ... 
  @media (max-width: 897px) {
    margin-top: 1vw;
    margin-bottom: 2.5vw;
    //padding: 13px 21px 14px; // px 
    padding: 1.5vw 2.5vw 1.6vw;
  }
  @media (max-width: 500px) {
    width: 100%;
    font-size: 4vw;
    padding: 2.9vw 8.3vw 3vw;
  }

  &:hover {
    background-color: #FE7B28;
    text-shadow: 0px 1.5px 0.5px #ab5e2a, 0px -1.5px 0.5px #ffaf7a, 1.5px 0px 0.5px #d68452, -1.5px 0px 0.5px #d68452;
  }


}

